    <Row type="flex">
        <Col span="8"><span>学员姓名: {{ appointmentResp.stuRealName || '--' }}</span></Col>
        <Col span="8"><span>年级: {{ appointmentResp.grade || '--' }}</span></Col>
        <Col span="8"><span>学员编号: {{ appointmentResp.stuCode || '--' }}</span></Col>
        <!-- 散诊跟进 -->
        <!-- <template v-if="operationType !== 2">
          <Col span="8"><span>是否参诊: {{ appointmentResp.isDiagnosisAttend === 1 ? '是' : '否' }}</span></Col>
          <Col span="8"><span>是否报名: {{ appointmentResp.isRegist === 1 ? '是' : '否' }}</span></Col>
          <Col span="8"><span>是否缴费: {{ appointmentResp.isPay === 1 ? '是' : '否' }}</span></Col>
        </template> -->
        <!-- 试听跟进 1 -->
