          <Col span=8><span>试听课程:
              <Tooltip :max-width="200" :content="appointmentResp.className" v-if="appointmentResp.className">
                  {{ appointmentResp.className.slice(0, 15) }}<template v-if="appointmentResp.className.length >15">...</template>
              </Tooltip>
            </span>
          </Col>
          <Col span="8"><span>授课类型: {{ classTypeMap[appointmentResp.classType] || '--' }}</span></Col>
          <Col span="8"><span>课次: {{ appointmentResp.curriculumNum || '--' }}</span></Col>
