          <Col span="8"><span>服务中心: {{ appointmentResp.serviceCenter || '--' }}</span></Col>
          <Col span="8"><span>教室: {{ appointmentResp.classroom || '--' }}</span></Col>
          <Col span="8"><span>是否取消: {{ appointmentResp.appointmentStatus === 5 ? '是' : '否' }}</span></Col>
          <Col span="8"><span>考勤状态: {{ attendStatus[appointmentResp.isAttendance] || '--' }}</span></Col>
          <Col span="24"></Col>
        </template>
        <Col style="margin-top: 10px;" v-if="followList.length"><h3 class="title">【跟进记录】</h3></Col>
        <Col style="width: 100%;min-height: 100px;position: relative;">
            <Spin size="large" fix v-if="!loaded"></Spin>
            <template v-else>
              <Row class="record-item" v-for="(item, index) in followList" :key="index">
                <Col span="12">操作时间: {{ item.operateTime || '--' }}</Col>
                <Col span="12">操作人: {{ item.empName }}</Col>
                <Col span="12">类型: {{ operateTypeObj[item.followType]}}</Col>
                <Col span="12" v-if="item.followType === 2 || item.followType === 9">维护任务: {{ item.taskNames }}</Col>
                <Col span="24"><Divider style="margin: 10px 0;background-color: #333;"></Divider></Col>
                <!-- 散诊:预约 1，散诊:跟进 2 -->
                <template v-if="item.followType === 1 || item.followType === 2">
                  <Col span="12">用户意向: {{ item.stuIntention }}</Col>
                  <Col span="12" v-if="item.followType === 1">渠道标签: {{ item.channelName }}</Col>
                  <Col span="24"></Col>
                  <Col span="12">预约诊断时间: {{ item.diagnosisPlanTime || '--' }}</Col>
                  <Col span="12">下次跟进时间: {{ item.nextFollowTime || '--' }}</Col>
                  <Col span="8">意向服务中心: {{ item.serviceCenter || '--' }}</Col>
                  <Col span="8">意向学期: {{ item.term || '--' }}</Col>
                  <Col span="8">意向学科: {{ item.subject || '--' }}</Col>
                  <Col span="12">备注: {{ item.remark || '--' }}</Col>
                </template>
                <!-- 散诊: 成绩录入 3，4，5 -->
                <template v-if="item.followType === 3 || item.followType === 4 || item.followType === 5">
                  <Col span="12">参诊时间: {{ appointmentResp.diagnosisTime }}</Col>
                  <Col span="12">参诊年份: {{ appointmentResp.year }}</Col>
                  <Col span="6">参诊学期: {{ appointmentResp.termName || '--' }}</Col>
                  <Col span="6">参诊年级: {{ appointmentResp.gradeName || '--' }}</Col>
                  <Col span="6">参诊学科: {{ appointmentResp.subjectName || '--' }}</Col>
                  <Col span="6">参诊成绩: {{ appointmentResp.levelDegreeName || '--' }}</Col>
                  <Col span="24">诊断报告:
                    <a v-if="appointmentResp.reportUrl" :href="appointmentResp.reportUrl" target="_blank">报告连接</a>
                    <span v-else>--</span>
                  </Col>
                </template>
                <!-- 预约试听 6 -->
                <template v-if="item.followType === 6">
                  <Col span="12">用户意向: {{ item.stuIntention || '--' }}</Col>
                  <Col span="12">渠道标签: {{ item.channelName || '--' }}</Col>
                  <Col span="24">下次跟进时间: {{ item.nextFollowTime || '--'}}</Col>
                  <Col span="24">备注: {{ item.remark || '--'}}</Col>
                </template>
                <!-- 修改试听考勤 7 -->
                <template v-if="item.followType === 7">
                  <Col span="24">考勤状态: {{ attendStatus[item.isAttendance] || '--' }}</Col>
                  <Col span="24">修改原因: {{ item.isAttendanceChangeReason || '--'}}</Col>
                </template>
                <!-- 取消试听 8 -->
                <template v-if="item.followType === 8">
                  <Col span="24">取消原因: {{ item.cancelReasonValue || '--' }}</Col>
                  <Col span="24">{{ item.cancelReasonExtra }}</Col>
                </template>
                <!-- 试听跟进维护 9 -->
                <template v-if="item.followType === 9">
                  <Col span="12">用户意向: {{ item.stuIntention }}</Col>
                  <Col span="12">下次跟进时间: {{ item.nextFollowTime || '--' }}</Col>
                  <Col span="8">意向服务中心: {{ item.serviceCenter || '--' }}</Col>
                  <Col span="8">意向学期: {{ item.term || '--' }}</Col>
                  <Col span="8">意向学科: {{ item.subject || '--' }}</Col>
