                  <Col span="8" v-if="item.notAttendReasonName">未出勤原因: {{ item.notAttendReasonName || '--' }}</Col>
                  <Col span="8" v-if="item.notConvertReasonName">未转化原因: {{ item.notConvertReasonName || '--' }}</Col>
                  <Col span="8" v-if="item.isReceive || item.isReceive === 0">是否接待: {{ item.isReceive ? '是':'否'}}</Col>
                  <Col span="8" v-if="item.notReceiveReasonName">未接待原因: {{ item.notReceiveReasonName || '--' }}</Col>
                  <Col span="12">备注: {{ item.remark || '--' }}</Col>
                </template>
              </Row>
              <Row class="record-item" v-if="appointmentApply">
                <Col span="12">操作时间: {{ appointmentApply.createTime || '--' }}</Col>
                <Col span="12">操作人: {{ appointmentApply.stuName }}</Col>
                <Col span="12">类型: 试听申请</Col>
                <Col span="24"><Divider style="margin: 10px 0;background-color: #333;"></Divider></Col>
                <Col span="6">渠道链接: {{ appointmentApply.applyChannel || '--' }}</Col>
                <Col span="6">推荐顾问: {{ appointmentApply.referrerName ||  '--' }}</Col>
                <Col span="6">推荐人: {{ appointmentApply.referrerStuName || '--' }}</Col>
                <Col span="6">推荐学员编号: {{ appointmentApply.referrerStuCode || '--' }}</Col>
                <Col span="6">年级: {{ appointmentApply.gradeName || '--' }}</Col>
                <Col span="6">学科: {{ appointmentApply.subject || '--' }}</Col>
                <Col span="6">授课类型: {{ classTypeMap[appointmentApply.classType] || '--' }}</Col>
                <Col span="6">意向服务中心: {{ appointmentApply.intendedServiceCenter || '--' }}</Col>
              </Row>
            </template>
        </Col>
        <Col style="margin-top: 10px;"><h3 class="title">【添加维护】</h3></Col>
        <Col style="margin-bottom: 10px;margin-top: 7px;" v-if="taskResp.length">
          维护任务 <Tag color="primary" v-for="(item, index) in taskResp" :key="index">{{ item.taskName}}</Tag>
        </Col>
        <Col>
            <MaintainTaskForm
                :operationType="operationType"
                :appointmentId="appointmentId"
                :studentDetail="studentDetail"
                @success="$emit('success')"
                @close="$emit('close')"
                :taskResp="taskResp"></MaintainTaskForm>
        </Col>
    </Row>
</template>

<script>
import { queryFollowDetail } from '@/api/appointment'
import MaintainTaskForm from './maintainTaskForm'
export default {
  name: 'FollowRecord', // 跟进记录
  components: {
    MaintainTaskForm
  },
  props: {
    // cityCode / areaId / uid / realName / stuCard
    studentDetail: {
      type: Object,
      default: () => {
        return {}
      }
    },
    appointmentId: {
      // 预约id
      type: [String, Number],
      default: ''
    },
    appointmentApplyId: {
      // 试听申请id
      type: [Number, String],
      default: ''
    },
    operationType: {
      // 跟进类型 {1: 试听跟进}
      type: Number,
      default: null
    }
  },
  data () {
    return {
      appointmentResp: {}, // 预约记录信息
      followList: [], // 跟进记录
      taskResp: [], // 待维护任务
      operateTypeObj: {
        // 操作类型
        1: '预约诊断', // 散诊
        2: '跟进维护', // 散诊
        3: '前台散诊成绩直接录入', // 散诊
        4: '成绩录入', // 散诊
        5: '线下报告成绩匹配', // 散诊
        6: '预约试听', // 试听
        7: '修改考勤', // 试听
        8: '取消预约', // 试听
        9: '跟进维护' // 试听
      },
      attendStatus: {
        // 是否考勤：1 准时上课， 2迟到， 0缺勤
        0: '缺勤',
        1: '准时上课',
        2: '迟到'
      },
      classTypeMap: {
        1: '在线',
        2: '双师',
        4: '面授'
      },
      appointmentApply: null, // 试听申请记录
      loaded: false
    }
  },
  mounted () {
    this.queryFollowDetail()
  },
  methods: {
    refresh () {
      this.queryFollowDetail()
    },
    queryFollowDetail () {
      const params = {
        appointmentId: this.appointmentId,
        cityCode: this.studentDetail.cityCode,
        operationType: this.operationType,
        appointmentApplyId: this.appointmentApplyId
      }
      queryFollowDetail(params)
        .then((res) => {
          this.followList = Object.freeze(res.followList) || []
          this.appointmentResp = Object.freeze(res.appointmentResp) || {}
          this.taskResp = Object.freeze(res.taskResp) || []
          this.appointmentApply = Object.freeze(res.appointmentApply) || null
          this.loaded = true
        })
        .catch((err) => {
          this.loaded = true
          this.$Message.error({ content: err.message, duration: 5 })
        })
    }
  }
}
</script>

