<template>
  <!-- 滚动组件 -->
  <div class="scroll-wrap" :style="cusStyle">
    <slot></slot>
  </div>
</template>

<script>
import { on, off } from '@/libs/util'
export default {
  props: {
    openResize: { // 是否开启监听resize事件
      type: Boolean,
      default: false
    },
    cusStyle: {
      type: Object,
      default: null
    }
  },
  mounted () {
    if (this.openResize) {
      this.getClientHeight()
      on(window, 'resize', this.getClientHeight)
    }
  },
  beforeDestroy () {
    if (this.openResize) {
      off(window, 'resize', this.getClientHeight)
    }
  },
  methods: {
    getClientHeight () {
      const clientHeight = document.body.clientHeight
      const clientWidth = document.body.clientWidth
      console.log(clientHeight)
      this.$emit('on-resize', { clientHeight, clientWidth })
    }
  }
}
</script>

<style scoped lang="less">
.scroll-wrap {
  overflow: hidden;
  max-height: calc(~"100vh - 30px");
}
</style>
