<template>
  <!-- 维护任务+散诊预约 -->
  <Row>
    <Form>
      <Col span="10">
        <FormItem label="用户意向" required>
          <Select
            style="width: 130px"
            v-model="formValidate.stuIntention"
            placeholder="请选择"
            >
            <Option
                :value="item"
                v-for="(item, index) in conditions.stuIntention"
                :key="index"
            >{{ item }}</Option>
            </Select>
        </FormItem>
      </Col>
      <Col :span="14">
        <FormItem label="下次跟进时间">
          <DatePicker
              type="datetime" placeholder="请选择" style="width: 200px"
              format="yyyy-MM-dd HH:mm:ss"
              v-model="formValidate.nextFollowTimeVal"
              :options="dateOptions"
              @on-change="dateChange($event, 'nextFollowTime')"></DatePicker>
        </FormItem>
      </Col>
      <Col span="8">
        <FormItem label="意向服务中心">
          <Select
            style="width: 120px;"
            v-model="formValidate.serviceCenterId"
            placeholder="请选择"
            @on-change="changeService"
            clearable
            filterable
            label-in-value
            >
            <Option
                v-for="(item,index) in serviceList"
                :value="item.serviceCenterId"
                :key="index"
            >{{ item.deptName }}</Option>
            </Select>
        </FormItem>
      </Col>
      <Col span="8">
        <FormItem label="意向学期">
          <Select
            v-model="formValidate.termId"
            style="width: 120px;"
            placeholder="请选择"
            @on-change="changeTerm"
            clearable
            label-in-value
            >
            <Option v-for="(item,index) in termList" :value="item.id" :key="index">{{ item.name }}</Option>
          </Select>
        </FormItem>
      </Col>
      <Col span="8">
        <FormItem label="意向学科">
          <Select
            v-model="formValidate.subjectId"
            style="width: 120px;"
            placeholder="请选择"
            @on-change="changeSubject"
            clearable
            label-in-value
            >
            <Option
              v-for="(item,index) in subjectList"
              :value="item.id"
              :key="index"
              >{{ item.name }}</Option>
            </Select>
        </FormItem>
      </Col>
      <Col span="24">
        <FormItem label="是否领取伴手礼">
          <Select
            style="width: 130px"
            v-model="formValidate.isGift"
            placeholder="请选择"
            >
            <Option :value="1">是</Option>
            <Option :value="0">否</Option>
            </Select>
        </FormItem>
      </Col>
      <Col span="8" v-if="isTaskExist(37)">
        <FormItem label="未出勤原因">
          <Select
            style="width: 130px"
            v-model="formValidate.notAttendReason"
            placeholder="请选择"
            clearable
            >
            <Option
                :value="item.id"
                v-for="(item, index) in reasons.notAttend"
                :key="index"
            >{{ item.name }}</Option>
            </Select>
        </FormItem>
      </Col>
      <Col span="8" v-if="isTaskExist(30)">
        <FormItem label="未转化原因">
          <Select
            style="width: 130px"
            v-model="formValidate.notConvertReason"
            placeholder="请选择"
            clearable
            >
            <Option
                :value="item.id"
                v-for="(item, index) in reasons.notConvert"
                :key="index"
            >{{ item.name }}</Option>
            </Select>
        </FormItem>
      </Col>
      <Col span="8" v-if="isTaskExist(31)">
        <FormItem label="是否接待">
          <Select
            style="width: 130px"
            v-model="formValidate.isReceive"
            placeholder="请选择"
            clearable
            >
            <Option :value="1">是</Option>
            <Option :value="0">否</Option>
            </Select>
        </FormItem>
      </Col>
      <Col span="8" v-if="isTaskExist(31) && formValidate.isReceive === 0">
        <FormItem label="未接待原因" required>
          <Select
            style="width: 130px"
            v-model="formValidate.notReceiveReason"
            placeholder="请选择"
            clearable
            >
            <Option
                :value="item.id"
                v-for="(item, index) in reasons.notReceive"
                :key="index"
            >{{ item.name }}</Option>
            </Select>
        </FormItem>
      </Col>
      <Col span="24">
        <FormItem label="备注" required>
          <Input :autosize="{minRows: 3}" :maxlength="500" v-model.trim="formValidate.remark" type="textarea"  placeholder="请记录和用户沟通的结论，便于后续维护中可持续查看。" />
        </FormItem>
      </Col>
    </Form>
    <Col span="24" style="text-align: center;">
        <Button type="primary" style="margin-right: 30px;" :loading="isLoading" @click="submit">提交</Button>
        <Button @click="cancel">取消</Button>
      </Col>
  </Row>
</template>

<script>
import { getLinkage } from '@/api/bd'
import { queryServiceByAreaCode, condition } from '@/api/diagnosis'
import Cookie from 'js-cookie'
import { addFollow } from '@/api/appointment'
import { reasonCondition } from '@/api/audition'
export default {
  name: 'MaintainTaskForm', // 任务维护、跟进
  props: {
    studentDetail: {
      type: Object,
      default: () => {
        // cityCode / areaId / uid / realName / stuCard
        return {}
      }
    },
    operationType: {
      // 跟进类型 1: 试听跟进
      type: Number,
      default: null
    },
    appointmentId: {
      // 预约id
      type: [Number, String],
      default: ''
    },

    // 需要维护的任务
    taskResp: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  data () {
    return {
      formValidate: {
        appointmentId: '', // 预约id
        stuIntention: '', // 学员意向
        nextFollowTime: '', // 下次跟进时间
        nextFollowTimeVal: '',
        serviceCenterId: '', // 意向服务中心id
        serviceCenter: '', // 意向服务中心
        termId: '', // 意向学期id
        term: '', // 意向学期
        subjectId: '', // 意向学科id
        subject: '', // 意向学科
        remark: '', // 备注
        stuCode: '', // 学员编号
        pyUid: '', // 学员编号
        stuRealName: '', //  学员真实姓名
        empNo: '', //  操作人员工编号
        empName: '', //  操作人员工
        notAttendReason: '',
        notConvertReason: '',
        notReceiveReason: '',
        isReceive: null, // 是否接待
        isGift: null // 是否领取伴手礼
      },
      serviceList: [],
      termList: [],
      subjectList: [],
      isLoading: false,
      conditions: {
        // 筛选条件
        stuIntention: [] // 用户意向
      },
      dateOptions: {
        disabledDate (date) {
          return date && date.valueOf() < Date.now() - 86400000
        }
      },
      reasons: {
        notAttend: [], // 未出勤
        notReceive: [], // 未接待
        notConvert: [] // 未转换
      }
    }
  },
  mounted () {
    this.formValidate.pyUid = this.studentDetail.uid
    this.formValidate.stuRealName = this.studentDetail.realName
    this.formValidate.stuCode = this.studentDetail.card
    this.initView()
    this.reasonCondition()
  },
  methods: {
    // 判断任务是否存在
    isTaskExist (taskType) {
      // 30 试听促转化 31 试听未转化 37 考勤确认
      let bol = false
      for (let obj of this.taskResp) {
        if (obj.taskType === taskType) {
          bol = true
          return bol
        }
      }
    },
    // 试听：未出勤原因、未转化原因、未接待原因
    async reasonCondition () {
      try {
        const data = await reasonCondition()
        this.reasons = Object.freeze(data)
      } catch (error) {
        this.$Message.error({
          content: `获取原因失败: ${err.message}`,
          duration: 5
        })
      }
    },
    cancel () {
      this.$emit('close')
    },
    dateChange (val, typeName) {
      this.formValidate[typeName] = val
    },
    initView () {
      this.getServiceCenterListHanlder()
      this.getTermListHanlder()
      this.getCondition()
    },
    // 筛选条件
    getCondition () {
      condition({})
        .then((res) => {
          this.conditions = Object.freeze(res)
        })
        .catch((err) => {
          this.$Message.error({ content: err.message, duration: 5 })
        })
    },
    submit () {
      const params = Object.assign({}, this.formValidate, {
        cityCode: this.studentDetail.cityCode,
        operationType: this.operationType,
        empNo: JSON.parse(Cookie.get('columbusUserInfo')).id,
        empName: decodeURI(JSON.parse(Cookie.get('columbusUserInfo')).name),
        appointmentId: this.appointmentId || undefined,
        taskResp: this.taskResp || []
      })

      if (!this.formValidate.stuIntention) {
        return this.$Message.warning({ content: '请选择用户意向' })
      }

      if (this.formValidate.isReceive === 0 && !this.formValidate.notReceiveReason) {
        return this.$Message.warning({ content: '请选择未接待原因' })
      }

      if (!this.formValidate.remark) {
        return this.$Message.warning({ content: '请填写备注' })
      }

      this.isLoading = true
      addFollow(params)
        .then((res) => {
          this.$Message.info({ content: '提交成功' })
          this.$emit('success')
          // 清空表单
          this.formValidate.stuIntention = ''
          this.formValidate.nextFollowTime = ''
          this.formValidate.nextFollowTimeVal = ''
          this.formValidate.serviceCenterId = ''
          this.formValidate.serviceCenter = ''
          this.formValidate.termId = ''
          this.formValidate.term = ''
          this.formValidate.subjectId = ''
          this.formValidate.remark = ''
          this.formValidate.notReceiveReason = ''
          this.formValidate.notAttendReason = ''
          this.formValidate.notConvertReason = ''
          this.formValidate.isGift = null
          this.formValidate.isReceive = null
        })
        .catch((err) => {
          this.$Message.error({ content: err.message, duration: 5 })
        })
        .finally(() => {
          this.isLoading = false
        })
    },
    //   意向服务中心改变
    changeService (data) {
      if (!data) {
        this.formValidate.serviceCenter = ''
        return
      }
      this.formValidate.serviceCenter = data.label
    },
    //   学期改变
    changeTerm (data) {
      this.formValidate.subjectId = ''
      this.subjectList = []
      if (!data) {
        this.formValidate.term = ''
        return
      }
      this.formValidate.term = data.label
      this.getSubjectListHanlder()
    },
    //   意向学科改变
    changeSubject (data) {
      if (!data) {
        this.formValidate.subject = ''
        return
      }
      this.formValidate.subject = data.label
    },
    //   获取学科
    getSubjectListHanlder () {
      let baseSubject = {
        cityCode: this.studentDetail.cityCode,
        termIds: this.studentDetail.termId,
        queryType: 's'
      }
      getLinkage(baseSubject)
        .then((res) => {
          this.subjectList = res
        })
        .catch((err) => {
          this.$Message.error('获取学科失败:' + err.message)
        })
    },
    //   获取学期
    getTermListHanlder () {
      let baseTerm = {
        cityCode: this.studentDetail.cityCode,
        queryType: 't'
      }
      getLinkage(baseTerm)
        .then((res) => {
          this.termList = res
        })
        .catch((err) => {
          this.$Message.error('获取学期失败:' + err.message)
        })
    },
    // 获取服务中心
    getServiceCenterListHanlder () {
      let ser = {
        cityCode: this.studentDetail.cityCode,
        areaCode: this.studentDetail.areaCode
      }
      queryServiceByAreaCode(ser)
        .then((res) => {
          this.serviceList = res
          this.serviceList.forEach((value, index) => {
            if (!value.address) {
              value.address = ''
            }
          })
        })
        .catch((err) => {
          this.$Message.error('获取服务中心失败:' + err.message)
        })
    }
  }
}
</script>
<style lang="less" scoped>
