<template>
    <div class="related-wrapper">
      <div class="item" v-for="(item, index) in stuList" :key="item.stuLoginName" :class="index ? 'mt10' : ''">
        <div class="stu-info">
          <Sex :sex="item.sex"></Sex>
          <div class="info">
            <div class="name">
              {{item.stuRealName || '--'}}  | {{item.cityName || '--'}} | {{item.readStatus === 0 ? '未在读' : '在读'}}
            </div>
            <div class="no">
              学员编号： {{item.stuLoginName}}
            </div>
          </div>
        </div>
        <div class="option">
          <Button style="margin-right: 10px" v-if="item.bindStatus === 1 && $store.state.roleRelativeData.userInfo.cityCode === item.areaCode" @click="bind(item)" :loading="isBinding">绑定</Button>
          <Button v-if="$store.state.roleRelativeData.userInfo.cityCode === item.areaCode" @click="toStuArchives(item)">学员档案</Button>
        </div>
      </div>
    </div>
</template>

<script>
import {
  queryRelateAccount
} from '@/api/crmStudent'
import {
  dispatchStudentToSelf, judgeDispatchStudentToSelf
} from '@/api/crm'
import Sex from './sex'
export default {
  name: 'relatedStu',
  components: {
    Sex
  },
  props: {
    cityCode: String,
    uid: Number,
    studentId: String
  },
  data () {
    return {
      stuList: [],
      bindStuParams: {},
      isBinding: false
    }
  },
  methods: {
    getRelatedStu () {
      queryRelateAccount({
        areaCode: this.cityCode,
        pyUid: this.uid,
        studentId: this.studentId
      }).then(data => {
        this.stuList = data
      }).catch(err => {
        this.$Message.error('查询关联学员错误：' + err.message)
      })
    },
    toStuArchives (userInfo) {
      if (this.$router) {
        this.$router.push({
          name: `applyStuArchivesForMulti`,
          params: {
            id: userInfo.stuLoginName
          },
          query: {
            card: userInfo.stuLoginName,
            cityCode: userInfo.areaCode
          }
        })
        this.$emit('close')
      }
    },
    bind (userInfo) {
      judgeDispatchStudentToSelf({
        uid: userInfo.pyUid,
        cityCode: userInfo.areaCode
      }).then(res => {
        if (res.bindingStatus) {
          this.bindStuParams = res
          this.isBinding = true
          dispatchStudentToSelf(this.bindStuParams).then(() => {
            userInfo.bindStatus = 2
            this.$Message.success({
              content: '绑定成功',
              duration: 5
            })
            this.$emit('close')
          }).catch(err => {
            this.$Message.error({
              content: '绑定失败：' + err.message,
              duration: 5
            })
          }).finally(() => {
            this.isBinding = false
          })
        } else {
          this.$Message.warning({
            content: '查询绑定状态错误：judgeDispatchStudentToSelf返回不可绑定',
            duration: 5
          })
        }
      }).catch(err => {
        this.$Message.error({
          content: '查询绑定失败：' + err.message,
          duration: 5
        })
      })
    }
  },
  mounted () {
    this.getRelatedStu()
  }
}
</script>

<style scoped lang="less">
  .related-wrapper {
    .mt10 {
      margin-top: 10px;
    }
    .item {
      display: flex;
      justify-content: space-between;
      height: 70px;
      padding: 10px;
      background: #4781f7;
      align-items: center;
      .stu-info {
        display: flex;
        .info {
          .name {
            font-size: 16px;
            color: #FFFFFF;
            margin-bottom: 5px;
          }
          .no {
            font-size: 14px;
            color: rgba(255, 255, 255, 0.85);
          }
        }
      }
    }
    .option {
      text-align: right;
    }
  }
</style>
