// 查询学员
import { queryClassDetail } from '@/api/clazz'
export default {
  data () {
    return {
      queryData: {
        card: '',
        cityCode: ''
      },
      classInfo: {
        type: Object,
        default: () => {
          return {}
        }
      }
    }
  },
  mounted () {
    this.initStu()
  },
  methods: {
    async initStu () {
      try {
        if (this.$route.query) {
          if (this.$route.query.card && this.$route.query.card !== 'undefined') {
            this.queryData.card = String(this.$route.query.card)
          }
          if (this.$route.query.cityCode && this.$route.query.cityCode !== 'undefined') {
            this.queryData.cityCode = String(this.$route.query.cityCode)
          }
          if (this.$route.query.classId) {
            this.classInfo = await queryClassDetail({ classId: this.$route.query.classId })
          }
          if (this.queryData.card && this.queryData.cityCode) {
            this.$nextTick(() => {
              this.$refs['stuSearch'].searchStuByUid({
                card: this.queryData.card,
                realName: '',
                phone: '',
                cityCode: this.queryData.cityCode,
                current: 1
              })
            })
          }
        }
      } catch (e) {
        this.$Message.error('初始化错误：' + e || e.message)
      }
    }
  }
}
