<template>
    <Form ref="form" :label-width="100" label-colon :model="formData" :rules="ruleValidate" class="wrapper">
      <FormItem label="选择课次" prop="selectedClassNumId">
        <RadioGroup v-model="formData.selectedClassNumId" type="button" class="class-times">
          <Radio v-for="item in classInfo" :label="item.id" :disabled="Number(item.cucSurplusAuditionCount) < 1" :key="item.id">课次{{ item.classNum }}</Radio>
        </RadioGroup>
      </FormItem>
      <Row>
        <Col :span="12">
          <FormItem label="主讲老师">{{ auditionClass.teachers.map(t => t.teacherName).join(',') }}</FormItem>
        </Col>
        <Col :span="12">
          <FormItem label="服务中心">{{ auditionClass.classroomServiceCenterName }}</FormItem>
        </Col>
      </Row>
      <Row>
        <Col :span="12">
          <FormItem label="上课时间">{{ auditionClass.classTimeDisplay }}</FormItem>
        </Col>
        <Col :span="12">
          <FormItem label="教室">{{ auditionClass.classroomName }}</FormItem>
        </Col>
      </Row>
      <FormItem label="用户意向" style="width: 300px" prop="stuIntention">
        <Select v-model="formData.stuIntention">
          <Option v-for="(item, index) in stuIntentions" :value="item" :key="index">{{ item }}</Option>
        </Select>
      </FormItem>
      <FormItem label="渠道标签" style="width: 300px" prop="channelId">
        <Select v-model="formData.channelId">
          <Option v-for="(item, index) in diagnosisChannels" :value="item.id" :key="index">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem label="下次跟进" style="width: 300px">
        <DatePicker
          type="datetime" placeholder="请选择" style="width: 200px"
          format="yyyy-MM-dd HH:mm:ss"
          :options="dateOptions"
          @on-change="dateChange"></DatePicker>
      </FormItem>
      <FormItem label="备注" prop="remark">
        <Input v-model="formData.remark" type="textarea" :rows="3" placeholder="记录与家长的沟通结论，便于后期维护"></Input>
      </FormItem>
      <FormItem style="text-align: right">
        <Button type="primary" style="margin-right: 10px" @click="appointmentAddHandler">提交</Button>
        <Button @click="$emit('cancel')">取消</Button>
      </FormItem>
    </Form>
</template>

<script>
import { getClassAudition } from '@/api/clazz'
import { appointmentAdd } from '@/api/appointment'
import { condition, queryDiagnosisChannels } from '@/api/diagnosis'
export default {
  name: 'audition',
  props: {
    auditionClass: Object,
    studentDetail: Object
  },
  computed: {
    userInfo () {
      return this.$store.state.user.userInfo
    }
  },
  data () {
    return {
      stuIntentions: [],
      diagnosisChannels: [],
      dateOptions: {
        disabledDate (date) {
          return date && date.valueOf() < Date.now() - 86400000
        }
      },
      formData: {
        selectedClassNumId: '',
        stuIntention: '',
        channelId: '',
        nextFollowTime: '',
        remark: ''
      },
      classInfo: [],
      ruleValidate: {
        selectedClassNumId: [
          { required: true, message: '请选择课次', trigger: 'blur' }
        ],
        stuIntention: [
          { required: true, message: '请选择用户意向', trigger: 'blur' }
        ],
        channelId: [
          { required: true, message: '请选择渠道标签', trigger: 'blur', type: 'number' }
        ],
        remark: [
          { required: true, message: '请输入备注', trigger: 'blur' },
          { message: '反馈内容最多可输入200字', max: 200, trigger: 'blur', type: 'string' }
        ]
      }
    }
  },
  methods: {
    appointmentAddHandler () {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          appointmentAdd({
            appointmentType: 1,
            areaCode: this.auditionClass.cityId,
            channel: this.diagnosisChannels.find(d => d.id === this.formData.channelId).name,
            channelId: this.formData.channelId,
            cityCode: this.auditionClass.cityId,
            detail: {
              cucId: this.formData.selectedClassNumId,
              studentId: this.studentDetail.studentId
            },
            gradeType: this.auditionClass.gradeTypeName,
            gradeTypeId: this.auditionClass.gradeTypeId,
            nextFollowTime: this.formData.nextFollowTime,
            planAdvisorName: this.userInfo.name,
            planAdvisorNo: this.userInfo.empNo,
            pyUid: this.studentDetail.uid,
            remark: this.formData.remark,
            stuIntention: this.formData.stuIntention,
            auditionType: this.auditionClass.auditionType
          }).then(() => {
            this.$Message.success('预约试听成功')
            this.$emit('success')
          }).catch(err => {
            this.$Message.error('预约试听失败：' + err.message)
          })
        }
      })
    },
    dateChange (val) {
      this.formData.nextFollowTime = val
    },
    _getCondition () {
      condition({}).then(res => {
        this.stuIntentions = Object.freeze(res.stuIntention)
      }).catch(err => {
        this.$Message.error('查询用户意向选项失败：' + err.message)
      })
    },
    // 渠道标签列表
    _queryDiagnosisChannels () {
      queryDiagnosisChannels({}).then(res => {
        this.diagnosisChannels = Object.freeze(res)
      }).catch(err => {
        this.$Message.error('查询渠道标签选项失败：' + err.message)
      })
    },
    _getClassAudition () {
      getClassAudition({
        classId: this.auditionClass.classId,
        cityCode: this.auditionClass.cityId
      }).then(data => {
        this.classInfo = data
      }).catch(err => {
        this.$Message.error('查询试听班级失败：' + err.message)
      })
    }
  },
  mounted () {
    this._getClassAudition()
    this._getCondition()
    this._queryDiagnosisChannels()
  }
}
</script>

